import React from 'react'
import { IoMdContrast } from 'react-icons/io'
import { Divider } from 'antd'

interface IHeaderProps {
  children?: React.ReactNode
  title?: string,
  accessibility?: boolean,
  onClickAccessibility?(itemName: string): void,
  height?: number | string
}

const styleItems: React.CSSProperties = {
  cursor: 'pointer'
}

function Header(props: IHeaderProps): JSX.Element {

  return (
    <header className="cegov-container-header" style={{ height: props.height }} >
      {
        props.accessibility &&
        (
          <div className="cegov-accessibility">
            <div className="cegov-accessibility-left">
              <span>Acessibilidade</span>
              <div
                style={styleItems}
                onClick={() => props.onClickAccessibility ? props.onClickAccessibility('A-') : false}
              >
                <IoMdContrast />
              </div>&emsp;
              <div
                style={styleItems}
                onClick={() => props.onClickAccessibility ? props.onClickAccessibility('A-') : false}
              >
                A-
              </div>&emsp;
              <div
                style={styleItems}
                onClick={() => props.onClickAccessibility ? props.onClickAccessibility('A') : false}
              >
                A
              </div>&emsp;
              <div
                style={styleItems}
                onClick={() => props.onClickAccessibility ? props.onClickAccessibility('A+') : false}
              >
                A+
              </div>&emsp;&emsp;
            </div>
            <div className="cegov-accessibility-right">
              <span>
                <a href="https://www.ceara.gov.br/" target="_blank">Portal do Governo</a>
              </span>
              <Divider type="vertical" style={{ width: '5px', fontWeight: 'bold' }} />
              <span>
                <a href="https://cearatransparente.ce.gov.br" target="_blank" >Ceará Transparente</a>
              </span>
              <Divider type="vertical" />
              <span>
                <a href="https://cearatransparente.ce.gov.br/portal-da-transparencia/ouvidoria?locale=pt-BR" target="_blank">Ouvidoria</a>
              </span>
            </div>
          </div>)
      }
      {
        props.children
      }
    </header >
  )
}

export default Header
